.main .container .slider {
  width: 755px;
  white-space: nowrap;
  overflow-x: hidden; }
  .main .container .slider img {
    height: 312px; }

.main .container .menu {
  padding: 0 0 0 70px;
  margin: 0;
  border-top: 3px solid #9dc5f0;
  background: #fff; }
  .main .container .menu li {
    display: inline-block;
    transition: 0.3s; }
    .main .container .menu li:not(:last-child) {
      margin-right: 50px; }
    .main .container .menu li a.active {
      position: relative; }
      .main .container .menu li a.active:before {
        content: "";
        position: absolute;
        left: 50%;
        top: -8px;
        width: calc(100% + 4px);
        transform: translateX(-50%);
        height: 1px;
        transition: 0.3s;
        background: #9dc5f0; }

.section {
  display: inline-block;
  max-width: 478px;
  background: #fff;
  padding-bottom: 1000px;
  margin-bottom: -1000px; }
  .section .container {
    padding: 34px 30px 100px 30px;
